<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";
import 'vue2-datepicker/index.css';
export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      type:1,
      max_questions:12,
      time2: new Date(),
      selected: [], // Array reference
      options: [
      ],
      selected1: [], // Array reference
      options1: [
      ],
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      description:'',
      descriptionru:'',
      descriptionen:'',
      short_description:'',
      short_descriptionru:'',
      short_descriptionen:'',
      is_country:false,
      is_europe:false,
      is_world:false,
      is_published:true
    };
  },
  methods: {
    trySubmitFile(e) {
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      form.append("description", this.description);
      form.append("abstraction", this.short_description);
      form.append("title", this.title);
      form.append("type", this.type);
      form.append("max_questions", this.max_questions);
      axios.patch(`${baseUrl}tests/${this.$route.params.id}/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(()=>{
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru

          form.append("description", this.descriptionru);
          form.append("abstraction", this.short_descriptionru);
          form.append("title", this.titleru);
          axios.patch(`${baseUrl}tests/${this.$route.params.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en

              form.append("description", this.descriptionen);
              form.append("abstraction", this.short_descriptionen);
              form.append("title", this.titleen);
              axios.patch(`${baseUrl}tests/${this.$route.params.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.$router.push('/test-category')
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
        .catch(()=>{
          this.$toast("Ma'lumotlar to'liq yuklanmagan", {
            timeout: 2000,
            type:"error"
          });
        })
    },

  },
  mounted() {
    axios.get(`${baseUrl}tests/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'oz'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.title=this.menus.title
        this.description=this.menus.description
        this.short_description=this.menus.abstraction
        this.type=this.menus.type
        this.max_questions=this.menus.max_questions
